import apiCore from '@/utils/apiCore'

const HTTP = new apiCore({
  baseURL: process.env.VUE_APP_URL,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
})
// 月租金统计
export function getMonthRent(params: any) {
  return HTTP.get('/buss/report/monthRent', params)
}
// 月租金统计明细
export function getMonthRentDetail(params: any) {
  return HTTP.get('/buss/report/monthRentDetail', params)
}
// 租客学历分析
export function getRentEducation(params: any) {
  return HTTP.get('/buss/report/rentEducation', params)
}
// 入驻率分析
export function rents(params: any) {
  return HTTP.get('/buss/report/rents', params)
}
