import { Component, Vue } from 'vue-property-decorator'
import { getMonthRent } from '@/api/report/reportApi'

@Component({
  components: {},
})
export default class amount extends Vue {
  private pageTitle = '月租金分析'
  private monthList = [
    new Date().getFullYear() + '-0' + 1,
    new Date().getFullYear() + '-0' + 2,
    new Date().getFullYear() + '-0' + 3,
    new Date().getFullYear() + '-0' + 4,
    new Date().getFullYear() + '-0' + 5,
    new Date().getFullYear() + '-0' + 6,
    new Date().getFullYear() + '-0' + 7,
    new Date().getFullYear() + '-0' + 8,
    new Date().getFullYear() + '-0' + 9,
    new Date().getFullYear() + '-0' + 10,
    new Date().getFullYear() + '-0' + 11,
    new Date().getFullYear() + '-0' + 12,
  ]
  private query = {
    month:
      new Date().getMonth() >= 9
        ? new Date().getFullYear() + '-' + (new Date().getMonth() + 1)
        : new Date().getFullYear() + '-0' + (new Date().getMonth() + 1),
  }
  private dataSource: any[] = []

  public mounted(): void {
    this.postMonthRents()
  }

  private async postMonthRents() {
    const res = await getMonthRent(this.query)
    this.dataSource = res.data
  }

  private toAmountDetail(scope: any) {
    Vue.ls.set('/report/amountDetail', {
      projectId: scope.row.projectId,
      month: this.query.month,
    })
    location.href = '/index.html/#/report/amountDetail'
  }
}
